.own-film-page {
  display: grid;
  grid-template-areas: 'poster poster details details details desc desc desc desc' 'trailer trailer trailer trailer trailer trailer trailer trailer trailer';
  margin: 0 auto;
  width: 80%;
  padding-top: 5rem;
  overflow: auto;
}
.own-film-page::-webkit-scrollbar {
  width: 6px;
}
.own-film-page::-webkit-scrollbar-thumb {
  background-color: #6e819b;
}
@media (max-width: 768px) {
  .own-film-page {
    grid-template-areas: 'poster' 'details' 'desc' 'trailer';
    width: 100%;
    padding-top: 0.5rem;
  }
}
@media (min-width: 769px) and (max-width: 993px) {
  .own-film-page {
    grid-template-areas: 'poster details' 'desc desc' 'trailer trailer';
    width: 95%;
    padding-top: 0.5rem;
  }
}
.own-film-page .own-film-desc {
  grid-area: desc;
  margin-left: 2rem;
  height: 100%;
  padding: 1rem;
}
@media (min-width: 769px) and (max-width: 993px) {
  .own-film-page .own-film-desc {
    margin-top: 1rem;
    margin-left: 0;
  }
}
@media (max-width: 768px) {
  .own-film-page .own-film-desc {
    margin-left: 0;
    margin-top: 1rem;
  }
}
.own-film-page .own-film-desc h2 {
  margin-top: 0;
}
.own-film-page .own-film-poster {
  grid-area: poster;
  margin: 0 1rem;
  height: 100%;
}
@media (min-width: 769px) and (max-width: 993px) {
  .own-film-page .own-film-poster {
    margin-left: 0;
  }
}
.own-film-page .own-film-poster img {
  width: 100%;
}
.own-film-page .own-film-poster button {
  position: absolute;
  background-color: transparent;
  border: none;
  outline: none;
  color: #c70303;
  font-size: 2rem;
  padding: 0.5rem;
}
@media (max-width: 768px) {
  .own-film-page .own-film-poster button {
    font-size: 4rem;
  }
}
.own-film-page .own-film-details {
  grid-area: details;
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (max-width: 768px) {
  .own-film-page .own-film-details {
    margin-top: 1rem;
  }
}
.own-film-page .own-film-details i {
  margin-left: 0.6rem;
  color: #f78606;
}
.own-film-page .own-film-details li {
  padding: 1rem;
}
.own-film-page .own-film-details li:nth-child(even) {
  background-color: rgba(22, 22, 75, 0.699);
}
.own-film-page .own-film-trailer {
  grid-area: trailer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 1rem;
}
.own-film-page .own-film-trailer iframe {
  width: 80%;
  height: 400px;
}
@media (max-width: 992px) {
  .own-film-page .own-film-trailer iframe {
    width: 100%;
    height: 300px;
  }
}
.own-film-page .own-blue {
  background-color: rgba(51, 51, 199, 0.699);
  border: 1px solid #0d0ddb;
  color: white;
}



