.own-background {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  background-color: transparent;
  overflow: hidden;
}
.own-background i {
  color: #f78606;
  position: relative;
  margin: 10px;
  font-size: 40px;
  opacity: 0.7;
}
.own-background i.anim1 {
  -webkit-animation: 4s ease-in-out infinite anim1;
          animation: 4s ease-in-out infinite anim1;
}
.own-background i.anim2 {
  -webkit-animation: 2.3s ease-in-out infinite alternate-reverse anim2;
          animation: 2.3s ease-in-out infinite alternate-reverse anim2;
}
.own-background i.anim3 {
  -webkit-animation: 6s ease-in-out infinite alternate-reverse anim3;
          animation: 6s ease-in-out infinite alternate-reverse anim3;
}
.own-background i.anim4 {
  -webkit-animation: 7s ease-in-out infinite alternate-reverse anim4;
          animation: 7s ease-in-out infinite alternate-reverse anim4;
}
.own-background i.anim5 {
  -webkit-animation: 5.4s ease infinite alternate-reverse anim5;
          animation: 5.4s ease infinite alternate-reverse anim5;
}
.own-background i.anim6 {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.own-background i.anim7 {
  -webkit-transform: rotate(154deg);
      -ms-transform: rotate(154deg);
          transform: rotate(154deg);
}
.own-background i.anim8 {
  -webkit-transform: rotate(245deg);
      -ms-transform: rotate(245deg);
          transform: rotate(245deg);
}
@-webkit-keyframes anim1 {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
@keyframes anim1 {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}
@-webkit-keyframes anim2 {
  to {
    -webkit-transform: matrix(0.5, 0, 0, 0.5, 20, 20);
            transform: matrix(0.5, 0, 0, 0.5, 20, 20);
  }
}
@keyframes anim2 {
  to {
    -webkit-transform: matrix(0.5, 0, 0, 0.5, 20, 20);
            transform: matrix(0.5, 0, 0, 0.5, 20, 20);
  }
}
@-webkit-keyframes anim3 {
  from {
    -webkit-transform: matrix(1, 0, 0, 1, 0, 0);
            transform: matrix(1, 0, 0, 1, 0, 0);
  }
  to {
    -webkit-transform: matrix(1, 0, 0, 1, 50, 0);
            transform: matrix(1, 0, 0, 1, 50, 0);
  }
}
@keyframes anim3 {
  from {
    -webkit-transform: matrix(1, 0, 0, 1, 0, 0);
            transform: matrix(1, 0, 0, 1, 0, 0);
  }
  to {
    -webkit-transform: matrix(1, 0, 0, 1, 50, 0);
            transform: matrix(1, 0, 0, 1, 50, 0);
  }
}
@-webkit-keyframes anim4 {
  from {
    -webkit-transform: matrix(1, 0, 0, 1, 0, 0);
            transform: matrix(1, 0, 0, 1, 0, 0);
  }
  to {
    -webkit-transform: matrix(1, 0, 0, 1, 0, 50);
            transform: matrix(1, 0, 0, 1, 0, 50);
  }
}
@keyframes anim4 {
  from {
    -webkit-transform: matrix(1, 0, 0, 1, 0, 0);
            transform: matrix(1, 0, 0, 1, 0, 0);
  }
  to {
    -webkit-transform: matrix(1, 0, 0, 1, 0, 50);
            transform: matrix(1, 0, 0, 1, 0, 50);
  }
}
@-webkit-keyframes anim5 {
  to {
    -webkit-transform: matrix(0.3, 0, 0, 0.3, 0, 0);
            transform: matrix(0.3, 0, 0, 0.3, 0, 0);
  }
}
@keyframes anim5 {
  to {
    -webkit-transform: matrix(0.3, 0, 0, 0.3, 0, 0);
            transform: matrix(0.3, 0, 0, 0.3, 0, 0);
  }
}



