.own-film-card {
  cursor: pointer;
}
.own-film-card .own-poster img {
  width: 100%;
}
.own-film-card .own-poster .own-rating {
  position: absolute;
}
.own-film-card .own-poster .own-rating i {
  display: block;
  padding: 0.5rem;
}
.own-film-card .own-desc {
  padding: 1rem 2rem;
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  font-family: 'Indie Flower';
  font-weight: bold;
}
.own-film-card .own-desc .gold {
  color: #f78606;
}



