.empty-poster {
  height: 20rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
  background-color: #2e2e2e;
  color: white;
  font-size: 1.7rem;
  font-weight: bold;
  padding: 0.5rem;
  text-align: center;
}
@media (max-width: 992px) {
  .empty-poster {
    height: 40rem;
    font-size: 2.5rem;
    padding: 1.5rem;
  }
}
@media (max-width: 768px) {
  .empty-poster {
    height: 35rem;
  }
}



