.own-film-panel {
  overflow: auto;
}
.own-film-panel::-webkit-scrollbar {
  width: 6px;
}
.own-film-panel::-webkit-scrollbar-thumb {
  background-color: #6e819b;
}
.own-film-panel .own-film-list .own-item,
.own-film-panel .own-film-list .own-item-sizer {
  margin: 2px;
  width: 48%;
}
@media (min-width: 769px) and (max-width: 992px) {
  .own-film-panel .own-film-list .own-item,
  .own-film-panel .own-film-list .own-item-sizer {
    width: 32.5%;
  }
}
@media (min-width: 993px) {
  .own-film-panel .own-film-list .own-item,
  .own-film-panel .own-film-list .own-item-sizer {
    width: 12%;
  }
}
.own-film-panel .own-loading-form {
  text-align: center;
  -ms-flex-line-pack: center;
      align-content: center;
  font-size: 3rem;
  color: #1b4be9;
}
.own-film-panel .own-more {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 1rem;
  color: #c70303;
  font-size: 3rem;
  cursor: pointer;
}
.own-film-panel .own-more:hover {
  background-color: rgba(0, 0, 0, 0.404);
}



